<script>
import abi from '../assets/abi.json';
import { ethers } from 'ethers';
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      isInstallMestaMask: window.ethereum != null
        && window.ethereum.isMetaMask == true,

      currentAddress: null,

      connectedContract: null,

      //ownerOf에 필요한 변수
      ownerOf_tokenId: null,
      ownerOf_result: null,

      //balanceOf에 필요한 변수
      balanceOf_tokenId: null,
      balanceOf_result: null,

     // mint에 필요한 변수
     mint_to : null,
     mint_tokenId : null,
     mint_uri : null,
     mint_result : null,
    }
  },
  methods: {
    async connectMetaMask() {
      console.log("connectMetaMask 호출");
      var account = await window.ethereum.request({ method: 'eth_requestAccounts' });
      console.log(account);
      if (account.length > 0) {
        this.currentAddress = account[0];
        console.log(this.currentAddress);
      }
      else {
        console.log("계정이 없네!");
      }
    },
    async connectContract() {
      console.log("ConnectContract 호출");

      var contractAddress = "0x71Ff88A2827AB1B744be4110B3D88C3338CF921f";
      var provider = new ethers.providers.Web3Provider(window.ethereum);

      var tempSigner = provider.getSigner();
      var connectContract = new ethers.Contract(contractAddress, abi, tempSigner);
      console.log(connectContract);
      this.connectedContract = connectContract;
    },
    async ownerOf() {
      console.log("ownerOf 호출");
      var ownerAddress = await this.connectedContract.ownerOf(Number(this.ownerOf_tokenId));
      console.log(ownerAddress);
    },
    async BalanceOf() {
      console.log("BalanceOf 호출");
      var BalanceAddress = await this.connectedContract.BalanceOf(Number(this.ownerOf_tokenId));
      console.log(BalanceAddress);
    },
     async mint() {
      console.log("mint 호출");
      var transaction = await this.connectedContract.mint(
                                                            this.mint_to,
                                                            this.mint_tokenId,
                                                            this.mint_uri);
      console.log(transaction);
      this.mint_result = "생성 대기중";
      await transaction.wait();

      this.mint_result = transaction;
    }
  }
}
</script>

